<template>
  <section class="chart-container">
    <el-row>
      <el-col :span="13">
        <div id="main" style="width:100%; height:800px;"></div>
      </el-col>
      <el-col :span="11">
        <div id="main2" style="width:100%; height:800px;"></div>
      </el-col>
      <el-col :span="12">
        <div id="chartColumn" style="width:100%; height:400px;"></div>
      </el-col>
      <el-col :span="12">
        <div id="chartBar" style="width:100%; height:400px;"></div>
      </el-col>
      <el-col :span="12">
        <div id="chartLine" style="width:100%; height:400px;"></div>
      </el-col>
      <el-col :span="12">
        <div id="chartPie" style="width:100%; height:400px;"></div>
      </el-col>
    </el-row>
  </section>
</template>

<script type="text/ecmascript-6">
  var echarts = require('echarts');
  require('echarts/map/js/china');
  //import '../../china.js'

  export default {
    data() {
      return {
        main:null,
        chartColumn: null,
        chartBar: null,
        chartLine: null,
        chartPie: null
      }
    },
    created() {
      var user = sessionStorage.getItem('user');
      if (user) {
      }
      else {
        this.$router.push({ path: '/login' });
      }
    },
    methods: {
      drawmainChart() {
        this.main=echarts.init(document.getElementById('main'));
        var geoCoordMap = [
          ['阿里云上海', [121.22551,31.771022]],
          ['百度云上海', [121.2991,30.950075]],
          ['阿里云北京', [116.589392,40.863174]],
          ['百度云北京', [117.178105,40.217526]],
          ['金山云北京', [115.651129,39.679441]],
          ['阿里云广州', [113.5107, 23.2196]]
        ];
        var Data = [
          [{name:'阿里云上海'}, {memory:100,number:95}],
          [{name:'百度云上海'}, {memory:50,number:90}],
          [{name:'阿里云北京'}, {memory:30,number:80}],
          [{name:'百度云北京'}, {memory:90,number:30}],
          [{name:'金山云北京'}, {memory:60,number:30}],
          [{name:'阿里云广州'}, {memory:80,number:30}],
        ];
        var point=[];
        geoCoordMap.forEach(
          function(item,i){
            point.push({
              name:item[0],
              type:'effectScatter',
              coordinateSystem:'geo',
              zlevel:2,
              rippleEffect: {
                brushType: 'stroke'
              },
              label: {
                normal: {
                  show: true,
                  position: 'right',
                  formatter: '{b}'
                }
              },
              symbolSize: function () {
                return Data[i][1].memory / 8;
              },
              data: item[1].map(function () {
                return {
                  name: Data[i][0].name,
                  value: item[1]
                };
              })
            })
          }
        );
        this.main.setOption({
          tooltip: {
            padding: 10,
            backgroundColor: '#222',
            borderColor: '#777',
            borderWidth: 1,
            formatter: function (obj) {
              var value = obj.data;
              return '<div style="border-bottom: 1px solid rgba(255,255,255,.3); font-size: 18px;padding-bottom: 7px;margin-bottom: 7px">'
                + obj.seriesName
                + '</div>'
                + '存储量：' + Data[obj.dataIndex][1].memory + 'G'+'<br>'
                + '文件个数：' + Data[obj.dataIndex][1].number + '<br>';
            }
          },
          title: {
            text: '数据地理分布'
          },
          geo: {
            map: 'china',
            label: {
              emphasis: {
                show: false
              }
            },
            roam:true,
            left:'3%',
            itemStyle: {
              normal: {
                areaColor: '#C4C4C4',
                borderColor: '#FFFFFF'
              }
            }
          },
          series: point

        });
      },
      drawmainChart2() {
        this.chartColumn = echarts.init(document.getElementById('main2'));
        this.chartColumn.setOption({
          title: { text: '云际间数据传输' },
          tooltip: {},
          animationDurationUpdate: 1500,
          animationEasingUpdate: 'quinticInOut',
          series : [
            {
              type: 'graph',
              layout: 'none',
              symbolSize: 50,
              label: {
                normal: {
                  show: true
                }
              },
              edgeLabel: {
                normal: {
                  textStyle: {
                    fontSize: 20
                  }
                }
              },
              data: [{
                name: '阿里云北京',
                x: 350,
                y: 100,
                value:30,
                itemStyle: {
                  normal: {
                    color: '#ff6600'
                  }
                },
                symbolSize:'50'
              },{
                name: '百度云北京',
                x: 450,
                y: 100,
                value:90,
                symbolSize:'90'
              },{
                name: '金山云北京',
                x: 400,
                y: 120,
                value:60,
                itemStyle: {
                  normal: {
                    color: '#09F7F7'
                  }
                },
                symbolSize:'60'
              }, {
                name: '阿里云上海',
                x: 500,
                y: 300,
                value:100,
                itemStyle: {
                  normal: {
                    color: '#ff6600'
                  }
                },
                symbolSize:'100'
              }, {
                name: '百度云上海',
                x: 550,
                y: 300,
                value:50,
                symbolSize:'50'
              }, {
                name: '阿里云广州',
                x: 350,
                y: 400,
                value:60,
                itemStyle: {
                  normal: {
                    color: '#ff6600'
                  }
                },
                symbolSize:'60'
              }],
              links: [{
                source: '阿里云北京',
                target: '金山云北京',
                value:5,
                z:100,
                lineStyle: {
                  normal: { width: 2, color:'#FFCC00' }
                }
              },{
                source: '金山云北京',
                target: '百度云北京',
                value:5,
                z:100,
                lineStyle: {
                  normal: { width: 1, color:'#2BD54D' }
                }
              },{
                source: '阿里云北京',
                target: '阿里云广州',
                value:5,
                z:100,
                lineStyle: {
                  normal: { width: 5, color:'#FFCC00' }
                }
              }, {
                source: '百度云上海',
                target: '百度云北京',
                value:10,
                z:100,
                lineStyle: {
                  normal: { width: 10, color:'#2BD54D'}
                }
              }, {
                source: '百度云北京',
                target: '阿里云上海',
                value:6,
                z:100,
                lineStyle: {
                  normal: { width: 6, color:'#FFCC00'}
                }
              }, {
                source: '阿里云上海',
                target: '阿里云北京',
                value:4,
                z:100,
                lineStyle: {
                  normal: { width: 4, color:'#FFCC00'}
                }
              }]

            }
          ]
        })
      },
      drawColumnChart() {
        this.chartColumn = echarts.init(document.getElementById('chartColumn'));
        this.chartColumn.setOption({
          title: { text: '存储量' },
          tooltip: {},
          xAxis: {
            data: ['阿里云上海','百度云上海', '阿里云北京', '百度云北京','金山云北京','阿里云广州']
          },
          yAxis: {},
          series: [{
            name: '存储量',
            type: 'bar',
            data: [100, 50, 30, 90, 60, 80]
          }]
        });
      },
      drawBarChart() {
        this.chartBar = echarts.init(document.getElementById('chartBar'));
        this.chartBar.setOption({
          title: {
            text: '用户流量',
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            }
          },
          legend: {
            data: ['上传量', '下载量']
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: {
            type: 'value',
            boundaryGap: [0, 0.01]
          },
          yAxis: {
            type: 'category',
            data: ['阿里云上海','百度云上海', '阿里云北京', '百度云北京','金山云北京','阿里云广州']
          },
          series: [
            {
              name: '上传量',
              type: 'bar',
              data: [18, 23, 29, 10, 13, 16]
            },
            {
              name: '下载量',
              type: 'bar',
              data: [19, 23, 31, 12, 13, 6]
            }
          ]
        });
      },
      drawLineChart() {
        this.chartLine = echarts.init(document.getElementById('chartLine'));
        this.chartLine.setOption({
          title: {
            text: ''
          },
          tooltip: {
            trigger: 'axis'
          },
          legend: {
            data: ['邮件营销', '联盟广告', '搜索引擎']
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日']
          },
          yAxis: {
            type: 'value'
          },
          series: [
            {
              name: '上传',
              type: 'line',
              stack: '总量',
              data: [120, 132, 101, 134, 90, 230, 210]
            },
            {
              name: '下载',
              type: 'line',
              stack: '总量',
              data: [220, 182, 191, 234, 290, 330, 310]
            },
            {
              name: '浏览',
              type: 'line',
              stack: '总量',
              data: [820, 932, 901, 934, 1290, 1330, 1320]
            }
          ]
        });
      },
      drawPieChart() {
        this.chartPie = echarts.init(document.getElementById('chartPie'));
        this.chartPie.setOption({
          title: {
            text: 'Pie Chart',
            subtext: '纯属虚构',
            x: 'center'
          },
          tooltip: {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          legend: {
            orient: 'vertical',
            left: 'left',
            data: ['上传', '下载', '操作']
          },
          series: [
            {
              name: '访问来源',
              type: 'pie',
              radius: '55%',
              center: ['50%', '60%'],
              data: [
                { value: 335, name: '上传' },
                { value: 310, name: '下载' },
                { value: 234, name: '操作' },

              ],
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        });
      },
      drawCharts() {
        this.drawmainChart()
        this.drawmainChart2()
        this.drawColumnChart()
        this.drawBarChart()
        this.drawLineChart()
        this.drawPieChart()
      },
    },

    mounted: function () {
      this.drawCharts()
    },
    updated: function () {
      this.drawCharts()
    }
  }
</script>

<style>
  .chart-container {
    width: 100%;
    float: left;
  }
  /*.chart div {
      height: 400px;
      float: left;
  }*/

  .el-col {
    padding: 30px 20px;
  }
</style>



