<template>
  <el-container>
    <el-header>
      <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
        <h3 style="float:left">Joint Cloud&nbsp;&nbsp;&nbsp;</h3>
        <el-menu-item index="1"><router-link to="/cloud">云际存储</router-link></el-menu-item>
        <el-submenu index="2">
          <template slot="title">云际监控</template>
          <el-menu-item index="2-1"><router-link to="/data/list">操作记录</router-link></el-menu-item>
          <el-menu-item index="2-2"><router-link to="/data/map">地理分布</router-link></el-menu-item>
        </el-submenu>
      </el-menu>
    </el-header>
    <el-main>
      <router-view></router-view>
    </el-main>
    <el-footer></el-footer>
  </el-container>

</template>

<script type="text/ecmascript-6">
  import ElContainer from "element-ui/packages/container/src/main"

  export default {
    components: { ElContainer },
    data() {
      return {
        activeIndex: '1',
        activeIndex2: '1'
      };
    },
    mounted() {
      var user = sessionStorage.getItem('user');
      if (user) {
        user = JSON.parse(user);
        this.sysUserName = user.name || '';
        this.sysUserAvatar = user.avatar || '';
      }
      else{
        this.$router.push({ path: '/login' });
      }
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      }
    }
  }
</script>

<style>
  a:link {
    text-decoration: none;
  }

  .router-link-active {
    text-decoration: none;
  }
  .el-menu{
    justify-content: center;
  }

</style>



