<template>
  <div>
    <template>

      <el-button type="primary" >
        <el-upload
          class="upload-demo"
          action="update"
        >点击上传
        </el-upload>
      </el-button>
      <el-button @click="create_dir">新建文件夹</el-button>&nbsp;&nbsp;
      <el-button
        type="text"
        @click="change_dir()"
      >全部文件</el-button>&nbsp;
      <span style="color:#409eff">/{{current.dir}}</span>
    </template>
    <template>
      <el-table  highlight-current-row :data="fileList" style="width: 100%;">
        <el-table-column prop="name" label="文件名" width="250">
          <template slot-scope="scope">
            <el-button @click="check_file(scope.row)" type="text" >{{scope.row.name}}</el-button>
          </template>
        </el-table-column>
        <el-table-column prop="isdir" label="文件类型" width="150">
        </el-table-column>
        <el-table-column prop="size" label="大小" width="100">
        </el-table-column>
        <el-table-column prop="ctime" label="修改时间" width="200">
        </el-table-column>
        <el-table-column prop="cloud" label="存储云端" width="200">
        </el-table-column>
        <el-table-column prop="md5_value" label="操作" width="300">
          <template slot-scope="scope">
            <el-button type="text" @click="delete_file(scope.row)">删除</el-button>
            <el-button type="text" @click="download_file(scope.row)">下载</el-button>
          </template>
        </el-table-column>
      </el-table>
    </template>
  </div>
</template>
<script type="text/ecmascript-6">
  import axios from 'axios';
  const ERR_OK = 0;
  export default {
    data() {
      return {
        fileList:[],
        bread: [
          {
            path: '/',
            desc: '全部文件'
          }
        ],
        current: {
          dir: '',
          page: '1',
          num: '100',
          desc: '1',
          order: 'size'
        },
        fileList: [],
      }
    },
    created() {
      var user = sessionStorage.getItem('user');
      if (user) {

      }
      else{
        this.$router.push({ path: '/login' });
      }
      this.fetch_data();
    },
    methods: {
      change_dir(){
        this.current.dir='';
        this.fetch_data();
      },
      check_file(row) {
        if(row.isdir=='文件夹') {
          if(this.current.dir!='')
            this.current.dir+='/';
          this.current.dir += row.name;
          this.fetch_data();
        }
        else
          this.download_file(row);
      },
      delete_file(row) {
        var params = this.current;
        if(row.isdir=='文件夹') {
          this.$confirm('此操作将删除该文件夹以及文件夹下全部文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            axios.post('/api/cloud/delete_dir',{dir:(params.dir=='')?row.name:params.dir+'/'+row.name}).then((response) => {
              console.log(response);
            });
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
            this.fetch_data();
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }
        else {
          this.$confirm('此操作将删除该文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            axios.post('/api/cloud/delete_file',{dir:(params.dir=='')?row.name:params.dir+'/'+row.name}).then((response) => {
              console.log(response);
            });
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
            this.fetch_data();
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }
      },
      download_file(row) {
        var params = this.current;
        axios.post('/api/cloud/download',{dir:(params.dir=='')?row.name:params.dir+'/'+row.name}).then((response) => {
          console.log(response.data);
        });
      },
      fetch_data: function () {
        var params = this.current;
        this.fileList=[];
        axios.post('/api/cloud/get', { params }).then((response) => {
          response = response.data;
          console.log(response);
          if (response.status === ERR_OK) {
            for(let key in response.result){
              this.fileList.push({
                name:response.result[key]['file_name'],
                isdir:(response.result[key]['isdir']==true)?'文件夹':'文件',
                size:(response.result[key]['isdir']==true)?'-':response.result[key]['size'],
                ctime:(response.result[key]['isdir']==true)?'-':new Date(parseInt(response.result[key]['ctime']) * 1000).toLocaleString().replace(/:\d{1,2}$/,' '),
                cloud:(response.result[key]['isdir']==true)?'-':response.result[key]['cloud']
              })
            }
          }
        });
      },
      create_dir() {
        var params = this.current;
        this.$prompt('请输入名称', '新建文件夹', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
        }).then(({ value }) => {
          axios.post('/api/cloud/create',{dir:(params.dir=='')?value:params.dir+'/'+value}).then((response) => {
            console.log(response);
          });
          this.$message({
            type: 'success',
            message: '新建文件夹: ' + value
          });
          this.fetch_data();
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          });
        });
      }
    }
  }
</script>

<style >
</style>



